* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: white;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url(../img/background.jpeg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.section {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header {
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80px;
}
.header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-transform: uppercase;
  margin-top: 30px;
}
.header .logo h1 {
  font-size: 3rem;
  position: relative;
  font-weight: 400;
}
.header .logo h1 span {
  letter-spacing: 14px;
}
.header .logo p {
  font-size: 0.8rem;
}

.main {
  width: 300px;
  height: 300px;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 50px;
  border: 8px solid white;
  padding: 20px;
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}

.container {
  display: flex;
  justify-content: center;
}

.contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.contacts a img {
  width: 75px;
  height: 75px;
}

.link-instagram {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 75px;
  height: 75px;
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 3px solid rgba(255, 255, 255, 0.9);
}
.link-facebook {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 75px;
  height: 75px;
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 3px solid rgba(255, 255, 255, 0.9);
}
.link-booksy {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 75px;
  height: 75px;
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 3px solid rgba(255, 255, 255, 0.9);
}
.link-maps {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 75px;
  height: 75px;
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 3px solid rgba(255, 255, 255, 0.9);
}

.logo-inst {
  width: 70%;
  height: 70%;
  background-image: url(../img/Instagram-logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.logo-booksy {
  width: 70%;
  height: 70%;
  background-image: url(../img/Booksy_Logomark.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.logo-facebook {
  width: 70%;
  height: 70%;
  background-image: url(../img/Facebook-icon.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.logo-maps {
  width: 70%;
  height: 70%;
  background-image: url(../img/icons8-google-maps-240.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.footer {
  opacity: 0;
  color: #f8f8f8;
  padding: 20px;
  gap: 20px;
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer .phone {
  touch-action: none;
}

@keyframes movingBox {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.glow {
  animation-name: movingBox;
  animation-iteration-count: 1;
  animation-duration: 1500ms;
}